@import url('https://fonts.googleapis.com/css2?family=Gochi+Hand&display=swap');
body {
    background-image: url('./pexels-asad-photo-maldives-1591370.jpg');
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
    background-size: cover;
    font-family: 'Gochi Hand', cursive;
}

.links {
    border: 2px solid rgb(148, 197, 243);
    background-color: rgba(148, 197, 243, 0.685);
    padding: 2px;
    font-size: 1.5em;
    margin: 0;
    display: flex;
    justify-content: left;
}

.links:hover {
    background-color: rgb(148, 197, 243);
}

.link {
    text-align: center;
    text-decoration: none;
    font-weight: bold;
    padding: 5px;
    color: darkblue;
}

.link:hover {
    color: darkviolet;
}

.drinksFun {
    color: darkblue;
    font-size: 2em;
    text-align: center;
    font-weight: bolder;
    font-style: oblique;
}